import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom'

function MainNav(){
    let navigate = useNavigate();
    return(
        <Navbar expand="lg" className="bg-body-tertiary">
        <Container>
          <Navbar.Brand onClick={() => {navigate('/')}} style={{cursor:'pointer'}}>My Shop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link onClick={() => {navigate('/')}}>Home</Nav.Link>
              <Nav.Link onClick={() => {navigate('/detail/0')}}>Detail</Nav.Link>
              <Nav.Link onClick={() => {navigate('/cart')}}>Cart</Nav.Link>
              <NavDropdown title="Event" id="basic-nav-dropdown">
                <NavDropdown.Item onClick={() => {navigate('/event/one')}}>
                    Event 1
                </NavDropdown.Item>
                <NavDropdown.Item onClick={() => {navigate('/event/two')}}>
                  Event 2
                </NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">
                  Separated link
                </NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
        
      </Navbar>
      
    )
}

export default MainNav