import './App.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import { createContext, use, useEffect, useState, lazy, Suspense } from 'react';
import { data } from './data.js';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom'
import MainNav from './pages/MainPage.js';
import swal from 'sweetalert';
import axios from 'axios';
import { useQuery } from 'react-query';

const Detail = lazy(() => import('./pages/Detail.js'));
const Cart = lazy(() => import('./pages/Cart.js'));



export let Context1 = createContext()


function App() {

  useEffect(() => {
    localStorage.setItem('watched', JSON.stringify([]))
  }, [])


  let [shoes, setShoes] = useState(data);
  let [remainder, setRemainder] = useState([10, 11, 12]);
  let navigate = useNavigate();
  let [clickButton, setClickButton] = useState(0);


  let result = useQuery({
    queryKey: ['userName'],
    queryFn: async () => {
      const response = await axios.get('https://codingapple1.github.io/userdata.json');
      return response.data;
    }
  });
  console.log(result)

  return (
    <div className="App">
      <MainNav />

      <div className='main-bg' onClick={() => { navigate('/') }} style={{ cursor: 'pointer' }}></div>
      <Suspense fallback={<div></div>}>
        <Routes>
          <Route path="/" element={
            <div>
              <Container>
                <Row>
                  {
                    shoes.map(function (shoes, i) {
                      return (
                        <Goods  key={shoes.id} shoes={shoes} imgURL={'/shoes' + (shoes.id + 1).toString() + '.jpg'} />
                      )
                    })
                  }

                </Row>
                {
                  clickButton == 0 ?
                    <button onClick={() => {
                      setClickButton(clickButton + 1)
                      axios.get('https://codingapple1.github.io/shop/data2.json')
                        .then((result) => {
                          let copy = [...shoes, ...result.data];
                          setShoes(copy);
                        })
                        .catch(() => {
                          console.log('  ')
                        })
                    }}></button>
                    : (clickButton == 1 ?
                      <button onClick={() => {
                        setClickButton(clickButton + 1)
                        axios.get('https://codingapple1.github.io/shop/data3.json')
                          .then((result) => {
                            let copy = [...shoes, ...result.data];
                            setShoes(copy);
                          })
                          .catch(() => {
                            console.log('  ')
                          })
                      }}></button>
                      : null
                    )
                }
              </Container>
            </div>} />
          <Route path="/detail/:id" element={
            <Context1.Provider value={{ remainder, shoes }}>
              <Detail shoes={shoes} />
            </Context1.Provider>
          } />
          <Route path="*" element={<div></div>}></Route>
          <Route path="/about" element={<About />}>
            <Route path="member" element={<div></div>}></Route>
            <Route path="location" element={<div></div>}></Route>
          </Route>
          <Route path="/event" element={<Event />}>
            <Route path="one" element={<div>   </div>}></Route>
            <Route path="two" element={<Link onClick={() => {
              swal(" ", "   !", "success");
            }}> </Link>}></Route>
          </Route>

          <Route path='/cart' element={<Cart />} />


        </Routes>
      </Suspense>

    </div>
  );

  function Event() {
    return (
      <div>
        <h4> </h4>
        <Outlet></Outlet>
      </div>
    )
  }

  function About() {
    return (
      <div>
        <h4></h4>
        <Outlet></Outlet>
      </div>
    )
  }

  function Goods(props) {
    let navigate = useNavigate();
    return (
      <div className="col-md-4">
        <img src={process.env.PUBLIC_URL + props.imgURL} width="80%"
          onClick={() => { navigate('/detail/' + props.shoes.id) }}
          style={{ cursor: 'pointer' }} />
        <h4>{props.shoes.title}</h4>
        <p>{props.shoes.content}</p>
        <p>{props.shoes.price}</p>
      </div>
    )
  }
}

export default App;
