import { configureStore, createSlice } from '@reduxjs/toolkit';
import user from './store/userSlice.js';
import { act } from 'react';


let userCart = createSlice({
    name: 'userCart',
    initialState: 
    [
        {id : 0, title : 'White and Black', count : 2},
        {id : 2, title : 'Grey Yordan', count : 1}
    ],
    reducers:{
        plusCount(state,action){
          let idx = state.findIndex((x) => x.id == action.payload)
          state[idx].count += 1
        },
        minusCount(state, action){
          let idx = state.findIndex((x) => x.id == action.payload)
          if(state[idx].count == 0){
            state[idx].count = 0
          }else{
            state[idx].count -= 1
          }
          
        },
        addCart(state, action){
          let idx = state.findIndex((x) => x.id == action.payload.id)
          if(idx == -1){
            state.push(action.payload)
          }else{
            state[idx].count += 1
          }
        },
        deleteItem(state, action){
          let idx = state.findIndex((x) => x.id == action.payload.id)
          state.splice(idx, 1);
        }
      }
})

export let {plusCount, minusCount, addCart, deleteItem} = userCart.actions

export default configureStore({
  reducer: { 
    userCart : userCart.reducer,
    user : user.reducer,
  }
}) 