import { createSlice } from "@reduxjs/toolkit";

//useState 
let user = createSlice({
    name : 'user',
    initialState : 'kim',
    reducers : {
        changeName(state, i){
            console.log(state);
            return 'john ' + state;
        }
    }
})

export let {changeName} = user.actions

export default user